"use client";

import { Button } from "@/components/ui/button";
import { Home, User, Clock, MessageSquare } from "lucide-react";
import { usePathname, useRouter } from "next/navigation";

const navItems = [
  {
    label: "Home",
    icon: Home,
    path: "/home",
  },
  {
    label: "My Trips",
    icon: Clock,
    path: "/my-trips",
  },
  {
    label: "Support",
    icon: MessageSquare,
    path: "/support",
  },
  {
    label: "Profile",
    icon: User,
    path: "/profile",
  },
];

export function BottomNav() {
  const router = useRouter();
  const pathname = usePathname();

  // Don't show bottom nav on these pages
  const hideOnPages = ["/", "/login", "/signup", "/booking-confirmation"];
  if (hideOnPages.includes(pathname)) {
    return null;
  }

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-white border-t h-16 px-4">
      <div className="flex justify-around items-center h-full">
        {navItems.map((item) => {
          const isActive = pathname === item.path;
          const Icon = item.icon;
          
          return (
            <Button
              key={item.path}
              variant="ghost"
              className={`flex flex-col items-center space-y-1 h-auto py-1 ${
                isActive ? "text-primary" : "text-muted-foreground"
              }`}
              onClick={() => router.push(item.path)}
            >
              <Icon className="h-5 w-5" />
              <span className="text-xs">{item.label}</span>
            </Button>
          );
        })}
      </div>
    </div>
  );
}
